/* Anomaly.css */
.container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .input-container {
    display: flex;
    margin-bottom: 10px;
  }
  
  .input-container input {
    flex: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .button {
    padding: 10px 20px;
    margin-right: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .button:hover {
    background-color: #0056b3;
  }
  
  .anomalies-list {
    list-style-type: none;
    padding: 0;
  }
  
  .anomalies-list li {
    margin-bottom: 10px;
  }
  
  .anomalies-list li .description {
    font-weight: bold;
  }
  
  .anomalies-list li .author {
    color: #777;
  }
  
  .resolved {
    background-color: #e0ffe0;
  }
  
  .not-approved {
    background-color: #ffe0e0;
  }
  
  .approve-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: green
  }
  
  .approve-button:hover {
    color: green;
  }
  
  .delete-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: red;
  }
  
  .delete-button:hover {
    color: darkred;
  }
  
  .solve-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: blue;
  }
  
  .solve-button:hover {
    color: darkblue;
  }

  .approved {
    background-color: rgb(255, 255, 176);
  }
  